import Vue from 'vue';
import Vuex from 'vuex';
import actions from './actions';
import getters from './getters';
import mutations from './mutations';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		dataUrl: window.dataUrl,		// 
		page: 2,										// 
		directUrl: '',							//  url
		resetUrl: '',								//  url

		lockList: false,						// 
		loading: false,			  			// 
		locking: false,							// 
		list: {},										// 
	},
	actions,
  getters,
  mutations
});



